<template>
    <div>
      <div class="am-background">
        <img class="am-bg" src="http://fkzzz.oss-cn-qingdao.aliyuncs.com/img/1074835438.jpg" />
      </div>
      <div class="am-form am-top">
        <div class="am-form-box">
          <label class="zhanghao">账号/手机号</label>
          <div class="am-account">
            <input id="txtSize" type="tel" class="txtSize"  placeholder="请输入您的账号/手机号码" maxlength="11">
            <img @click="accountCancel" class="am-account-cancel" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <label class="mima">密码</label>
          <div class="am-form-group am-passWord">
            <input id="am-pwd" type="password" class="am-pwd" placeholder="填写密码" maxlength="16">
            <img @click="passwordCancel" id="passwordCancel" class="am-password-cancel" :src="login_07"/>
            <img @click="passwordShow" class="am-password-show" src="../../assets/images/Login/Login_06.png"/>
          </div>
          <p class="am-loginBtn">
            <button type="button" @click="loginBtn" id="login-button" class="am-btn am-btn-default">登&nbsp;录</button>
          </p>
        </div>

        <div class="footEnd">
          <router-link :to="Register">
            <a href="javascript:;">注册</a>
          </router-link>
          <router-link :to="ForgetPassword">
            <a href="javascript:;">忘记密码</a>
          </router-link>
          <!--<a href="/visit/setpassword">忘记密码?</a>-->
        </div>
        <div class="otherLogin">
          <!--<a><img @click="aa" class="qq" src="../../assets/images/Login/Login_03.png"/></a>-->
          <a><img class="weixin" @click="weixinLogin" src="../../assets/images/Login/Login_04.png"/></a>
        </div>
      </div>
    </div>
</template>
<script>
  import login_05 from '@/assets/images/Login/Login_05.png'
  import login_07 from '@/assets/images/Login/Login_07.png'
  function setCookie(name, value, days) {
    var d = new Date;
    d.setTime(d.getTime() + 60*60*1000*days);
    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
  }
  var urlhost = process.env.API_AJAX;
    export default {
      name: 'login',
        data() {
            return {
              aa:true,
              login_05:login_05,
              login_07:login_07,
            }
        },
      props:{
        Register: {
          type: Object,
          default () {
            return {
              name: 'register'
            }
          }
        },
        ForgetPassword: {
          type: Object,
          default () {
            return {
              name: 'forgetpassword'
            }
          }
        },
      },
      components:{

      },
      mounted:function () {
      },
      methods: {
        saveWwjtoken: function(token){
          this.$store.dispatch('saveWwjtoken', token);
        },
        saveUserId: function(userId){
          this.$store.dispatch('saveUserId', userId);
        },
        weixinLogin:function () {
//          var lacoUrl = "http://fkzzz.cn/";
//          window.location.href = "http://weixin.fkzzz.cn/user/oauth?url="+lacoUrl;//微信授权登录
          $.ajax({
            type: 'POST',
            url: "http://weixin.fkzzz.cn/share/getticket",
            data$Type: "jsonp",
            data: {
            },
            success: function (response) {
              var mess = JSON.parse(response);
              console.log(mess)
            },
          });

        },
        accountCancel:function () {
          $("#txtSize").val("");
        },
        passwordCancel:function () {
          if(this.aa == true){
            $("#am-pwd").attr("type","text");
            $("#passwordCancel").attr("src",this.login_05);
            this.aa=false;
          }else{
            $("#am-pwd").attr("type","password");
            $("#passwordCancel").attr("src",this.login_07);
            this.aa=true;
          }

        },
        passwordShow:function () {
          $("#am-pwd").val("");
        },
        loginBtn:function () {
          var _this = this
          var phone = $('#txtSize').val();
          var password = $('#am-pwd').val();
          if(phone === '' || password === ''){
            _this.$alert("内容不能为空！");

          }else{
            $.ajax({
              type: 'POST',
              url: urlhost+"user/phonelogin?channel_id=fkzzz",
              data$Type: "jsonp",
              data: {
                'phone': phone,
                "password":password
              },
              success: function (response) {
                var mess = JSON.parse(response);
                console.log(mess)
                if (mess.status == 0) {
                  _this.$alert(mess.message)
                }else{
                  _this.saveWwjtoken(mess.message.token); // 把wawajitoken存到vuex里面
                  _this.saveUserId(mess.message.user_id); // 把userId存到vuex里面
                  setCookie("wawajitoken",mess.message.token,8);
                  setCookie("getdiamond",mess.message.getdiamond);
                  setCookie("port",mess.message.port);
                  _this.$layer.msg("登录成功!");
                 _this.$router.push({
                    path:"/"
                  })
//                  var urlBack = _this.$route.query.redirect;
//                  console.log(urlBack)
//                if(urlBack === undefined){
//                  window.location.href = "/"
//                }else {
//                  window.location.href = "http://localhost:8081/#"+urlBack
//                }
                }
              },
            });

          }
        }
      }

    }
</script>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .am-background{
    padding: 0;
    width: 100%;
    height: n(300);
  }

  .am-bg{
    width: 100%;
    height: n(300);
  }


  .am-top{
    width: 90%;
    margin: auto;
    margin-top: n(35);
  }

  .am-form-box{
    width: 100%;
    margin-bottom: 0;
  }

  .am-account{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 90%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }

  .zhanghao{
    margin-top: n(20);
    font-weight: bold;
    display: inline-block;
    font-size: n(24);
    color: #424242;
  }
  .mima{
    margin-top: n(20);
    @extend .zhanghao
  }


  .am-form-box .am-passWord{
    width: 100%;
    height: n(105);
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    input{
      border: none;
      outline: none;
      width: 70%;
      height: n(70);
      font-size: n(28);
      color: #b0b0b0;
    }
    img{
      width: n(50);
      height: n(50);
    }
  }


  .am-loginBtn{
    width: 100%;
    margin: n(80) 0 0 0;
  }

  .am-loginBtn button{
    width: 100%;
    height: n(80);
    background: #fee837;
    font-size: n(30);
    border: none;
  }
  .footEnd{
    width: 100%;
    margin-top: n(20);
    display: flex;
    justify-content: space-between;
    a{
      font-size: n(24);
    }
  }

  .otherLogin{
    margin-top: n(50);
    height: n(65);
    width: 100%;
    display: inline-flex;
    justify-content: center;
    a{
      height: n(65);
      float: left;
      .qq{
        width: n(65);
        height: n(65);
        margin-right: n(50);
      }
      .weixin{
        width: n(65);
        height: n(65);
      }
    }
  }

</style>



